.TodoItem {
  border: 1px solid greenyellow;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active {
  color: greenyellow;
}

.Icon-delete {
  position: absolute;
  right: 0;
}
.Icon-delete:hover {
  color: red;
}
.Icon-check:hover {
  color: greenyellow;
}
