.LoadingTodo-container {
    border: 1px solid greenyellow;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
  }
  
  .LoadingTodo-text {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 25px;
    line-height: 24px;
    font-weight: 700;
  }
  
  .LoadingTodo-completeIcon, .LoadingTodo-deleteIcon {
    border-radius: 50px;
    height: 48px;
    width: 48px;
  }
  
.LoadingTodo-completeIcon {
    position: absolute;
    left: 12px;
}

.LoadingTodo-deleteIcon {
    position: absolute;
    right: 0;
}

.LoadingTodo-container, .LoadingTodo-completeIcon, .LoadingTodo-deleteIcon {
    background: linear-gradient(90deg, greenyellow, orange);
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
}

@keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}